/** CONTAINER **/

.container{
      width: 100%;
      height: 100vh;
      display: grid;
      place-content: center;
}

.box_center{
      gap: 2rem;
      align-items: center;
}

.aside_bar{
      width: 14rem;
      flex-direction: column;
      gap: .1rem;
}

.aside_bar__letter{
      width: 14rem;
      height: 14rem;
      position: relative;
}

.letter_front,
.letter_back{
      width: 14rem;
      height: 14rem;
      padding: 1rem;
}

.letter_front{
      position: relative;
      z-index: 2;
      background: rgba(255,255,255,.1);
      transition: .2s ease-in-out;
}

.aside_bar__letter:hover .letter_front{
      transform: translateX(-100%);
      cursor: pointer;
}

.letter_front:before{
      width: 12rem;
      height: 12rem;
      background: rgba(255,255,255,.1);
      top: 1rem;
      left: 1rem;
      backdrop-filter: blur(8px);
      opacity: 0;
      transition: .2s ease-in-out;
}

.aside_bar__letter:hover .letter_front:before{
      opacity: 1;
}

.letter_front span,
.letter_back span{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;
      font-size: 10rem;
      font-weight: 800;
      cursor: pointer;
}

.letter_front span{
      background: var(--light);
      color: var(--theme-200);
}

.letter_back span{
      color: rgba(0,0,0,.5);
}

.aside_bar__letter:nth-child(even) .letter_front span{
      background: var(--theme-100);
      color: var(--light);
}



.letter_back{
      position: absolute;
      top: 0;
      left: 0;
}

.main_content{
      width: 45rem;
      height: 44rem;
      position: relative;
}

.main_content:before{
      width: 28rem;
      height: 44rem;
      border-radius: 1.5rem;
      border: solid .3rem var(--light);
      top: 0;
      right: 0;
      box-sizing: border-box;
}

.main_left{
      width: 20rem;
      height: 21rem;
      padding: 1rem;
      flex-direction: column;
      flex-shrink: 0;
      background: var(--dark-100);
      position: relative;
      margin-top: 50%;
      transform: translateY(-50%);
      border-radius: 1rem;
}

.user_data{
      width: 18rem;
      height: 6rem;
      background: var(--light);
      position: relative;
      transform: translateY(-4rem);
      gap: 1rem;
      padding: .6rem;
      border-radius: 999px;
}

.user_data__img,
.user_data__img img{
      width: 4.8rem;
      height: 4.8rem;
      border-radius: 50%;
}

.user_data__img{
      position: relative;
}

.online{
      width: 1.4rem;
      height: 1.4rem;
      border-radius: 50%;
      position: absolute;
      bottom: 0;
      right: 0;
      z-index: 2;
      border-radius: 50%;
}

.online.green{
      background: var(--green);
}

.online.red{
      background: var(--red-200);
}

.user_data__name{
      position: relative;
}

.user_data__name h2{
      color: var(--dark-100);
      font-size: 3.4rem;
      line-height: 3.4rem;
      font-weight: 200;
}

.user_data__name span{
      color: var(--theme-100);
      position: absolute;
      top: 2.6rem;
      padding-left: .5rem;
      font-size: 1.6rem;
}

.user_about{
      height: 7rem;
}

.user_about p{
      font-size: 1.4rem;
}

.btn_button{
      height: 4.6rem;
      border-radius: 999rem;
      text-transform: uppercase;
      border: solid .2rem var(--light);
      background: none;
      color: var(--light);
      font-size: 1.6rem;
      transition: .2s ease-in-out;
}

.btn_button:hover{
      background: var(--light);
      color: var(--theme-200);
}

.main_right{
      width: 28rem;
      height: 100%;
}

.photo_banner{
      width: 28rem;
      height: 39.4rem;
      position: absolute;
      border-radius: 1rem;
      overflow: hidden;
      top: 50%;
      right: 0;
      transform: translate(4rem, -50%)
}

.video_box{
      width: 28rem;
      height: 15.8rem;
      background: var(--dark-200);
      position: absolute;
      bottom: 0;
      right: 0;
      border-radius: 1rem;
      transform: translate(30%, 30%);
      overflow: hidden;
}

.video_content{
      position: fixed;
      width: 35.4rem;;
      height: 100vh;
      min-height: 80rem;
      background: var(--dark-200);
      z-index: 10;
      padding: 1.5rem;
      transform: translateX(-100%);
      transition: .5s ease-in-out;
}

.video_content.active{
      transform: translateX(0);
}

.header_video{
      height: 5rem;
      width: 100%;
      align-items: center;
      justify-content: space-between;
}

.header_video__title *{
      font-size: 3rem;
      color: var(--red-200);
}

.header_video__title{
      align-items: center;
      gap: 1rem;
}

.header_video__title h3{
      color: var(--light);
      font-weight: 300;
      font-size: 2rem;
}

.close_video{
      width: 4rem;
      height: 4rem;
      align-items: center;
      justify-content: center;
      background: none;
}

.close_video *{
      color: var(--light);
      font-size: 3rem;
}

.main_video{
      width: 100%;
      height: calc(100% - 10rem);
      min-height: calc(100% - 10rem);
      overflow-y: auto;
      margin-top: 2rem;
}

.videos_list{
      width: 100%;
      height: auto;
      flex-direction: column;
      gap: 2rem;
}

.video_row{
      width: 100%;
      height: auto;
      flex-direction: column;
      align-items: center;
      gap: 1rem;
}

.video_row__box{
      width: 30rem;
      height: 16.9rem;
      background: var(--dark-200);
      border-radius: 1rem;
      overflow: hidden;
}

.video_row span{
      font-size: 1.2rem;
      text-align: center;
      text-transform: uppercase;
}