
@import url('https://fonts.googleapis.com/css2?family=Fjalla+One&family=Mr+Dafoe&family=Squada+One&family=Yanone+Kaffeesatz:wght@200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
:root{
  /** fonts */
  --ft-1: 'Fjalla One', sans-serif;
  --ft-2: 'Yanone Kaffeesatz', sans-serif;
  --ft-3: 'Squada One', cursive;
  --ft-4: 'Mr Dafoe', cursive;
  --ft-5: 'Poppins', sans-serif;

  /** colors */
  --green: #45d985;
  --light: #fff;
  --red-200: #d11e22;
  --red-100: #e90d0f;
  --alpha: rgba(0,0,0,.1 );
  --grys-300: #a2c2c8;
  --grys-200: #c9d8db;
  --grys-100: #fcffff;

  /** default theme */
  --theme-200: #4a1212;
  --theme-100: #671313;
  --theme-alpha: rgba(71,73,160,0.17);
  --secundary: rgba(0,0,0,.5);
  --dark-200: #111111;
  --dark-100: #1e1e1e;
  --gradient-135: linear-gradient(135deg, var(--theme-200), var(--secundary));
  --gradient-45: linear-gradient(135deg, var(--secundary), var(--theme-200));
  --gradient-btn: linear-gradient(135deg, var(--secundary), var(--theme-100));
  --gradient-filter: linear-gradient(135deg, var(--grys-300), var(--theme-100));
  --gradient-2: linear-gradient(135deg, var(--theme-200), var(--light));

  /** social color*/
  --facebook: #3b5995;
  --twitter: #1da1f2;
  --google: #dd4b39;
  --youtube: #c4302b;
  --vimeo: #1ab7ea;
  --dribbble: #ea4c89;
  --github: #333;
  --instagram: #c13584;
  --pinterest: #e60023;
  --flickr: #0063dc;
  --bitbucket: #205081;
  --linkedin: #0e76a8;
  --whatsapp: #12af0a;
  --tumblr: #35465c;
  --tiktok: #222;
  --discord: #5865f2;
  
  /** globals */
  font-family: var(--ft-5);
  font-size: 62.5%;
  color: var(--light);

}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  min-height: 98rem;
  min-height: 100vh;
  overflow-x: hidden;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--theme-200);
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  border: none;
  outline: none;
  cursor: pointer;
  font-family: var(--ft-2);
}

iframe{
  border: none;
  outline: none;
  border-radius: 1rem;
}

.bg_grad{
  display: inline-block;
  color: transparent;
  background: var(--gradient-45);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
}

.flex{
  display: flex;
}

::-webkit-scrollbar{
  width: .8rem;
  background: var(--theme-200);
  border-radius: .8rem
}

::-webkit-scrollbar-thumb{
  background: var(--secundary);
  border-radius: .8rem;
}